@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

input[type=text] {
    margin: 10px 10px 0 10px;
    padding: 0 5px;
    width: 65%;
    max-width: 300px;
    height: 35px;
}

input[type=date] {
    margin: 10px;
    padding: 0 5px;
    width: 65%;
    max-width: 300px;
    height: 35px;
}

input[type=checkbox] {
    margin: 5px 5px 5px 0;
}

.addTaskButton {
    margin: 0 0 20px 10px;
    padding: 10px 20px;
    text-transform: uppercase;
    font-size: 16px;
    color: rgb(0, 214, 221);
    background-color: #fff;
    border: 2px solid #000;
    cursor: pointer;
}

.addTaskButton:hover {
    border-color: rgb(0, 214, 221);
    color: #000;
}

.validation {
    margin-left: 10px;
    font-size: 10px;
    color: #f00;
}

@media (max-width: 768px) and (orientation: portrait) {
    input[type=text] {
        margin: 0 10px 0 20px;
        height: 35px;
        font-size: 16px;
    }

    input[type=date] {
        margin: 5px 10px 0 20px;
        height: 35px;
        font-size: 16px;
    }

    .addTaskButton {
        margin: 0 0 0 20px;
        padding: 10px 20px;
        font-size: 16px;
    }

    .form label {
        font-size: 16px;
    }
}

@media (max-width: 414px) and (orientation: portrait) {
    input[type=text] {
        margin: 0 10px 0 10px;
        height: 25px;
        font-size: 12px;
    }

    input[type=date] {
        margin: 5px 10px 0 10px;
        height: 25px;
        font-size: 12px;
    }

    .addTaskButton {
        margin: 0 0 0 10px;
        padding: 5px 20px;
        font-size: 12px;
    }

    .form label {
        font-size: 12px;
    }
}

@media (max-width: 823px) and (orientation: landscape) {
    .form {
        margin: 0;
    }

    .addTaskButton {
        margin: 0 0 0 10px;
        padding: 5px 20px;
        font-size: 12px;
    }

    .form label {
        font-size: 12px;
    }
}

@media (max-width: 667px) and (orientation: landscape) {
    input[type=text] {
        margin: 0 10px 0 10px;
        height: 25px;
        font-size: 12px;
    }

    input[type=date] {
        margin: 5px 10px 0;
        height: 25px;
        font-size: 12px;
    }

    .addTaskButton {
        margin: 0 0 0 10px;
        padding: 5px 20px;
        font-size: 12px;
    }

    .form label {
        font-size: 12px;
    }
}
.taskListContainer {
    margin: 20px auto;
    width: 95%;
}

.taskListContainer p {
    padding: 5px 0;
    font-size: 14px;
}

.tasksTable {
    width: 100%;
}

.titleRow {
    background-color: rgb(240, 168, 86);
    text-align: center;
}

.tasksTable td {
    padding: 5px 0;
    text-align: center;
    font-size: 12px;
    border-bottom: 1px dotted rgba(240, 168, 86, .5);
}

.titleRow td {
    padding: 10px 0;
    font-size: 14px;
    font-weight: bold;
    border: none;
}

.titleRow td:nth-of-type(1) {
    width: 7%;
}

.titleRow td:nth-of-type(2) {
    width: 48%;
}

.titleRow td:nth-of-type(3) {
    width: 15%;
}

.titleRow td:nth-of-type(4) {
    width: 15%;
}

.titleRow td:nth-of-type(5) {
    width: 15%;
}

button.actions {
    margin: 0 10px;
    width: 15px;
    height: 15px;
    font-size: 15px;
    background-color: transparent;
    border: none;
    cursor: pointer;
}

button.actionDone {
    color: rgb(0, 230, 57);
}

button.actionDelete {
    color: rgb(230, 57, 0);
}

button.actionDone:hover,
button.actionDelete:hover {
    opacity: .5;
}

@media (max-width: 768px) and (orientation: portrait) {
    .taskListContainer {
        margin: 10px auto;
    }

    .taskListContainer p {
        font-size: 16px;
    }

    .titleRow td {
        font-size: 15px;
    }

    .tasksTable td {
        font-size: 13px;
    }
}

@media (max-width: 414px) and (orientation: portrait) {
    .taskListContainer p {
        font-size: 12px;
    }

    .titleRow td {
        font-size: 12px;
    }

    .tasksTable td {
        font-size: 11px;
    }
}

@media (max-width: 360px) and (orientation: portrait) {
    .taskListContainer {
        margin: 5px auto;
    }

    .titleRow td {
        font-size: 10px;
    }

    .tasksTable td {
        font-size: 10px;
    }
}

@media (max-width: 823px) and (orientation: landscape) {
    .taskListContainer {
        margin: 5px auto;
    }

    .taskListContainer p {
        font-size: 12px;
    }

    .titleRow td {
        font-size: 11px;
    }

    .tasksTable td {
        font-size: 11px;
    }
}

@media (max-width: 667px) and (orientation: landscape) {
    .taskListContainer p {
        font-size: 12px;
    }
}

@media (max-width: 568px) and (orientation: landscape) {
    .taskListContainer p {
        font-size: 10px;
    }

    .titleRow td {
        font-size: 10px;
    }

    .tasksTable td {
        font-size: 10px;
    }
}
.cat {
    text-align: center;
}

.categoryButton {
    margin: 10px;
    padding: 5px 10px;
    width: 23%;
    font-size: 16px;
    text-transform: uppercase;
    color: #000;
    background-color: #fff;
    border: 1px solid #000;
    cursor: pointer;
}

.categoryButton:hover {
    border-color: #999;
    color: #999;
}

.categoryButton i.fa-check-square {
    color: rgb(0, 230, 50);
}

.categoryButton i.fa-trash-alt {
    color: rgb(230, 50, 0);
}

.categoryButton i.fa-exclamation {
    color: rgb(0, 50, 230);
}

.categoryButton i.fa-tasks {
    color: rgb(230, 230, 0);
}

.categoryButton:nth-of-type(1) {
    background-color: rgba(230, 230, 0, 0.05);
}

.categoryButton:nth-of-type(2) {
    background-color: rgba(0, 230, 50, 0.05);
}

.categoryButton:nth-of-type(3) {
    background-color: rgba(230, 50, 0, 0.05);
}

.categoryButton:nth-of-type(4) {
    background-color: rgba(0, 50, 230, 0.05);
}

@media (max-width: 768px) and (orientation: portrait) {
    .cat {
        margin: 10px 0;
        text-align: center;
    }

    .categoryButton {
        margin: 5px;
        padding-top: 10px;
        padding-bottom: 10px;
        width: 23%;
        font-size: 16px;
    }
}

@media (max-width: 414px) and (orientation: portrait) {
    .cat {
        margin: 10px 0;
        text-align: center;
    }

    .categoryButton {
        margin: 5px;
        padding: 5px 10px;
        width: 45%;
        font-size: 12px;
    }
}

@media (max-width: 823px) and (orientation: landscape) {
    .cat {
        margin: 10px 0;
        text-align: center;
    }

    .categoryButton {
        margin: 5px;
        width: 23%;
        font-size: 14px;
    }
}

@media (max-width: 667px) and (orientation: landscape) {

    .categoryButton {
        margin: 5px;
        width: 45%;
    }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Open Sans', sans-serif;
}

:root {
  font-family: 'sans-serif';
  background-color: rgba(241, 167, 6, 0.07);
}

.app {
  margin: 0 auto;
  max-width: 1024px;
  min-height: 100vh;
}

.tasksContainer {
  display: flex;
  flex-direction: column;
  text-align: left;
}

.category {
  flex-basis: 5vh;
  line-height: 5vh;
  border-bottom: 1px dotted #ddd;
}

.addTask {
  flex-basis: 20vh;
  padding: 20px 0;
  border-bottom: 1px dotted #ddd;
}

.taskList {
  flex-basis: 62vh;
  flex-grow: 1;
}

.footer {
  flex-basis: 5vh;
  line-height: 5vh;
  border-top: 1px dotted #ddd;
  text-transform: lowercase;
  text-align: center;
  font-size: 14px;
}

@media (max-width: 667px) and (orientation: landscape) {
  .footer {
    flex-basis: 8vh;
    line-height: 8vh;
    font-size: 10px;
  }
}
